.app-footer {
    background: linear-gradient(to bottom, rgba($clr2, 0.5) 0%, rgba($clr2, 0.6) 70%, rgba($clr2, 1) 100%);
    // background-color: $clr2;

    display: flex;
    align-items: flex-end;
    justify-content: center;
    bottom: 2px;
    width: 100%;
    position: fixed;

    img {
        padding: rem(10px);
    }
    .contacts-btn,
    .go-back-btn {
        border-radius: 50%;
        background-color: $clr2;
    }

    @include for-mobile-layout {
        img {
            padding: rem(5px);
        }
        .contacts-btn {
            height: rem(80px);
            width: rem(80px);
        }
        .go-back-btn {
            height: rem(60px);
            width: rem(60px);
        }
    }
}
