.home-page {
    display: flex;
    flex-direction: column;
    gap: rem(10px);
    align-items: center;

    border-radius: rem(10px);
    padding: rem(10px);

    .guest-welcome {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        align-items: center;
        width: fit-content;
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
        a {
            cursor: pointer;
        }
        button {
            border-radius: rem(10px);
            padding: rem(10px);
            background-color: $clr6;
        }
    }

    .user-balance {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
    }

    &.logged-user {
        text-align: center;
    }

    button {
        width: fit-content;
    }

    .transactions-table {
        position: relative;
        a {
            cursor: pointer;
            color: blue;
            position: absolute;
            padding: 5px;
            right: 0;
            top: 0;
        }
    }

    @include for-mobile-layout {
        padding: rem(5px);
        gap: rem(5px);
        .guest-welcome {
            h1 {
                font-size: rem(23px);
            }
        }
    }
}
